</template>

<script>
import ContentCatalogSon from '@/components/ContentCatalogSon.vue';
import ContentInstallSon from '@/components/ContentInstallSon.vue';

export default {
    components: {
        ContentCatalogSon,
        ContentInstallSon,
    },
    props: {
        maxNum: {
            type: Number
        },
        chapter: {
            type: Array
        }
    },
    data() {
        return {
            value: 0,
            showCatalog: false,
            showInstall: false,
            condition: true,
        }
    },
    mounted() {
        this.value = this.$store.state.UpDownChapter;
    },
    methods: {
        onChange(value) {
            this.$store.commit('AcquireUpDownChapter',{
                index:value
            });
            // console.log('当前值：' + value);
        },
        switchoverNight() {
            this.condition = !this.condition
            this.$store.commit('ClicknightJudge')
        },
        catalog() {
            this.showCatalog = true 
        },
        install() {
            this.showInstall = true 
        },
        leftClick() {
            var num = this.$store.state.UpDownChapter;
            if(num>0) {
                this.$store.commit('UpDownChapterMinus')
                this.$toast(this.chapter[1][num-1].title);
            }else {
                console.log();
                this.$toast(this.chapter[1][num].title);
            }
        },
        rightClick() {
            var num = this.$store.state.UpDownChapter;
            if(num<this.maxNum) {
                this.$store.commit('UpDownChapterAdd');
                this.$toast(this.chapter[1][num+1].title);
            }else {
                this.$toast(this.chapter[1][num].title);
            }
        }
    },
    watch: {
        "$store.state.UpDownChapter"() {
            this.value = this.$store.state.UpDownChapter+1;
            console.log('当前值：' + this.value);
        }
    }
}
</script>

<style lang="less">
    #ChapterContentBottom {
        position: fixed;
        left: 0;
        bottom: 0;
        width: 100%;
        .ChapterContentBottom {
            
            background-color: #323232;
            padding: 20px 10px;
            .Slider {
                display: flex;
                justify-content: center;
                align-items: center;
                span {
                    flex: 1;
                    padding: 0 20px;
                    font-size: 14px;
                    color: #eeeeee;
                }
                .van-slider {
                    flex: 4;
                    .custom-button {
                        width: 16px;
                        height: 16px;
                        border-radius: 50%;
                        background-color: #fca000;
                    }
                }
            }
            .Icon {
                display: flex;
                justify-content: space-around;
                align-items: center;
                margin-top: 20px;
                span {
                    width: 30px;
                    display: flex;
                    flex-wrap: wrap;
                    justify-content: center;
                    i {
                        margin-top: 4px;
                        font-size: 14px;
                        font-style: normal;
                        color: #eeeeee;
                    }
                }
                .van-overlay {
                    background-color: rgba(0,0,0,0.4);
                }
            }

        }
    }
</style>