<template>

    <!-- 书架内容子组件 -->

    <div id="BookrackContentSon">

        <div class="BookrackContentSon" @click="BookrackContentSonClick(item.id)" >

            <div class="img">
                <van-image radius="2px" :src="item.img" />
            </div>

            <div class="text">
                <p>{{item.title}}</p>
                <span>{{item.author}}</span>
            </div>

        </div>

    </div>

</template>

<script>
export default {
    props: {
        item: {
            type: Object
        },
    },
    methods: {
        BookrackContentSonClick (id) {
            this.$store.commit('JudgeError');
            this.$router.push({
                path: "/noveldetails",
                query: {
                    id
                }
            })
        }
    },
}
</script>

<style lang="less">
    #BookrackContentSon {
        width: 100%;
        padding: 10px 20px;
        .BookrackContentSon {
            width: 100%;
            display: flex;
            justify-content: center;
            .img {
                width: 20%;
                display: flex;
                box-shadow: 0 0 1px #888888;
                .van-image {
                    vertical-align: bottom;
                }
            }
            .text {
                width: 80%;
                padding: 10px 10px;
                display: flex;
                flex-wrap: wrap;
                align-items: center;
                p {
                    width: 100%;
                    font-size: 18px;
                    display: -webkit-box;
                    -webkit-box-orient: vertical;
                    -webkit-line-clamp: 2;
                    overflow: hidden;
                    text-overflow: ellipsis;
                }
                span {

                    width: 100%;
                    font-size: 14px;
                    color: #666666;
                }
            }
        }
        
    }
</style>