<template>

    <!-- 章节设置组件 -->

    <div id="ContentInstallSon">

        <div class="ContentInstallSon">

            <div class="ContentInstallSonTop">
                <p>字号</p>
                <van-stepper
                    v-model="value"
                    max="3"
                    button-size="22px"
                    theme="round"
                    disable-input
                    @change="Onchange"
                />
            </div>

            <div class="ContentInstallSonCenter">
                <p>背景</p>
                <ul>
                    <li
                        v-for="(item, index) in items"
                        :key="index"
                        :style="{backgroundColor:item.color,borderColor:verdict==item.color?tinct:''}"
                        @click="ClickItem(item.color)"
                    ></li>
                </ul>
            </div>

        </div>

    </div>

</template>

<script>
export default {
    data() {
        return {
            value: 0,
            checked: true,
            tinct: "#fca000",
            verdict: "#e3d9bc",
            items: [
                {
                    color: "#f5f5f5"
                },
                {
                    color: "#cfd9e0"
                },
                {
                    color: "#e3d9bc"
                },
                {
                    color: "#ddebd6"
                },
                {
                    color: "#f3d8d8"
                },
            ],
            size: [ "20px","24px","28px" ],
        }
    },
    methods: {
        ClickItem(n) {
            this.$store.commit('AcquireContentBackgroundColor',{
                color: n
            });
            this.verdict = n
        },
        Onchange(n) {
            this.$store.commit('AcquireContentFontSize',{
                size: this.size[n-1]
            });
        }
    },
}
</script>

<style lang="less">
    #ContentInstallSon {
        padding: 24px 20px;
        width: 100%;
        height: 100%;
        background-color:  #fefefe;
        .ContentInstallSon {
            width: 100%;
            height: 100%;
            display: flex;
            flex-wrap: wrap;
            align-content: space-around;
            .ContentInstallSonTop {
                width: 100%;
                display: flex;
                align-items: center;
            }
            .ContentInstallSonCenter {
                width: 100%;
                display: flex;
                align-items: center;
                ul {
                    display: flex;
                    li {
                        width: 28px;
                        height: 28px;
                        border: 2px solid transparent;
                        border-radius: 50%;
                        margin-right: 20px;
                    }
                }
            }
            p {
                padding-right: 20px;
                color: #323233;
            }
        }
    }
</style>