<template>

    <!-- 推荐独有的组件 -->

    <div id="ExclusiveRecommend">
        
        <TitleComponent>
            <slot></slot>
        </TitleComponent>

        <ExclusiveRecommendSon
            v-for="(item, index) in items"
            :key="index"
            :item="item"
        >
        </ExclusiveRecommendSon>

    </div>
</template>

<script>
import TitleComponent from '@/components/TitleComponent.vue';
import ExclusiveRecommendSon from '@/components/ExclusiveRecommendSon.vue';

export default {
    components: {
        TitleComponent,
        ExclusiveRecommendSon,
    },
    props: {
        Romance:{
            type: Array
        },
        ShuangWen:{
            type: Array
        },
        HighMark:{
            type: Array
        },
    },
    data() {
        return {
            items: []
        }
    },
    mounted() {
        if(this.Romance){
            this.items = this.Romance
        }
        if(this.ShuangWen){
            this.items = this.ShuangWen
        }
        if(this.HighMark){
            this.items = this.HighMark
        }
    },
}
</script>

<style lang="less">
    #ExclusiveRecommend {
        padding: 10px;
    }
</style>