<script>
export default {
    props: {
        Citems:{
            type:Array
        }
    },
    data() {
        return {
            items:[1,2,3],
            justify:false
        }
    },
    mounted() {
        var canvas = this.$refs.canvas
        var ctx = canvas.getContext("2d")
        ctx.fillStyle="#ffda33";
        ctx.strokeStyle = "#ffda33";
        ctx.lineWidth = 1;
        ctx.fillRect(0,0,300,104);
        ctx.moveTo(0, 104);
        ctx.quadraticCurveTo(150, 114, 300, 104);
        ctx.fill();
        ctx.stroke();
    },
    methods: {
        AccessDetails(id) {
            this.$store.commit('JudgeError')
            this.$router.push({
                path:"/noveldetails",
                query: {
                    id
                },
            })
        }
    },
}
</script>

