<template>

    <!-- 小说章节内容 -->

    <div id="ChapterContent">

        <div>
            <ChapterContentTop
                v-show="ShowUpDown"
                :ContentTop="ContentTop"
            ></ChapterContentTop>
            <ChapterContentBottom
                v-show="ShowUpDown"
                :chapter="chapter"
                :maxNum="maxNum"
            ></ChapterContentBottom>
        </div>

        <ChapterContentCenter
            :cpContent="cpContent"
            :title="title"
        ></ChapterContentCenter>

    </div>

</template>

<script>
import ChapterContentTop from '@/components/ChapterContentTop.vue';
import ChapterContentBottom from '@/components/ChapterContentBottom.vue';
import ChapterContentCenter from '@/components/ChapterContentCenter.vue';

export default {
    components: {
        ChapterContentTop,
        ChapterContentBottom,
        ChapterContentCenter
    },
    data() {
        return {
            ContentTop: [],
            ShowUpDown: null,
            cpContent: [],
            title: null,
            chapter: [],
            ChapterNum: 0,
            maxNum: null,
        }
    },
    // mounted() {
    //     var id = this.$router.history.current.query.id;
    //     //历史记录
    //     var strId = localStorage.getItem('historyId');
    //     var arrId = strId.split(',');//历史id

    //     var strChapter = localStorage.getItem('historychapter');
    //     var arrChapter = strChapter.split(',');//历史看到的章节

    //     for(var i=0;i<arrId.length;i++) {
    //         if(arrId[i]===id) {
    //             console.log(+arrChapter[i]);
    //             this.$store.commit('AcquireUpDownChapter',{
    //                 index: +arrChapter[i]
    //             });
    //         }
    //     }
    // },
    activated() {

        this.$store.commit('AcquireCoatingJudge',{
            judge: true
        })

        this.ContentTop = [];
        this.chapter = [];

        this.ShowUpDown = this.$store.state.ShowUpDown;
        this.$store.commit('ClickShowUpDown');
        this.$store.commit('JudgeError');

        var id = this.$router.history.current.query.id;
        this.ContentTop.push(id);
        
        

        this.ChapterNum = null;
        this.ChapterNum = this.$store.state.UpDownChapter;



        this.axios.get(`book-info/${id}`).then((respaonse)=>{
            this.chapter.push(respaonse.data.title);
            this.ContentTop.push(respaonse.data.title);
        })

        this.axios.get(`book-sources?view=summary&book=${id}`).then((respaonse)=>{
            this.axios.get(`book-chapters/${respaonse.data[0]._id}`).then((respaonse)=>{

                this.maxNum = null;
                if(this.chapter.length<2) {
                    this.chapter.push(respaonse.data.chapters);
                }
                this.maxNum = respaonse.data.chapters.length;

                this.UpDownHttp();

            })
        })

    },
    methods: {
        UpDownHttp() {
            var http = encodeURIComponent(this.chapter[1][this.ChapterNum].link);
            this.axios.get(`chapters/${http}`).then((respaonse)=>{
                this.cpContent = [];
                this.title = respaonse.data.chapter.title;

                var arr = respaonse.data.chapter.cpContent.split("");
                var newArr = ""
                for(let i=0; i<arr.length; i++) {
                    if(arr[i]===arr[113]&&arr[i+1]===arr[114]) {
                        newArr += "/"
                    }else {
                        newArr += arr[i]
                    }
                }

                this.cpContent = newArr.split("/");
            }).finally(()=>{
                this.$store.commit('AcquireCoatingJudge',{
                    judge: false
                })
            })
        }
    },
    watch: {
        "$store.state.ShowUpDown"(n) {
            this.ShowUpDown = n;
        },
        "$store.state.UpDownChapter"(n) {
            this.ChapterNum = n;

            this.$store.commit('AcquireCoatingJudge',{
                judge: true
            })

            this.UpDownHttp();

        },
    }
}
</script>

<style lang="less">
    #ChapterContent {
        height: 100%;
    }
</style>