<template>

    <!-- 搜索得到的内容 -->

    <div id="TerminativeSearch">

        <ul>
            <li
                v-for="(item, index) in Searchbooks"
                :key="index"
                @click="click(item.id)"
            >
                <div class="img">
                    <img :src="item.img">
                </div>

                <div class="text">
                    <div class="top">
                        <span class="name">{{item.title}}</span>
                    </div>
                    <div class="center">
                        <p>
                            {{item.shortIntro}}
                        </p>
                    </div>
                    <div class="bottom">
                        <span class="writer">{{item.author}}</span>
                    </div>
                </div>
            </li>
        </ul>

    </div>

</template>

<script>
export default {
    props: {
        Searchbooks: {
            type:Array
        }
    },
    data() {
        return {
        }
    },
    methods: {
        click(id) {
            this.$store.commit('JudgeError')
            this.$router.push({
                path:"/noveldetails",
                query: {
                    id
                },
            })
        }
    },
}
</script>

<style lang="less">
    #TerminativeSearch {
        background-color: #ffffff;
        ul {
            li {
                display: flex;
                padding: 10px;
                .img {
                    width: 24%;
                    box-shadow: 0 0 1px rgba(0, 0, 0, 0.2);
                    display: flex;
                    align-items: center;
                    img {
                        vertical-align: bottom;
                    }
                }
                .text {
                    width: 74%;
                    margin-left: 10px;
                    display: flex;
                    flex-wrap: wrap;
                    align-items: center;
                    .top {
                        width: 100%;
                        .name {
                            width: 80%;
                            font-family: "黑体";
                            font-size: 18px;
                            color: #222222;
                            display: -webkit-box;
                            -webkit-box-orient: vertical;
                            -webkit-line-clamp: 1;
                            overflow: hidden;
                            text-overflow: ellipsis;
                        }
                    }
                    .center {
                        width: 100%;
                        p {
                            font-size: 14px;
                            line-height: 24px;
                            color: #999999;
                            display: -webkit-box;
                            -webkit-box-orient: vertical;
                            -webkit-line-clamp: 2;
                            overflow: hidden;
                            text-overflow: ellipsis;
                        }
                    }
                    .bottom {
                        width: 100%;
                        .writer {
                            font-size: 13px;
                            color: #999999;
                        }
                    }
                }
            }
        }
    }
    
</style>