<template>

    <!-- 搜索书籍 -->

    <div id="SearchBooks">

        <SearchBooksHead></SearchBooksHead>

        <div v-if="condition" >
            <HotSearch
                :hotSearch="hotSearch"
            ></HotSearch>
            <SearchHistory
                :searchHistory="searchHistory"
            ></SearchHistory>
        </div>
        <div v-else >
            <SearchCoverage
                :keywords="keywords"
                :Searchbooks="Searchbooks"
            ></SearchCoverage>
        </div>

    </div>

</template>

<script>
import SearchBooksHead from '@/components/SearchBooksHead.vue';
import HotSearch from '@/components/HotSearch.vue';
import SearchHistory from '@/components/SearchHistory.vue';
import SearchCoverage from '@/components/SearchCoverage.vue';

export default {
    components: {
        SearchBooksHead,
        HotSearch,
        SearchHistory,
        SearchCoverage,
    },
    data() {
        return {
            condition: true,
            hotSearch: [],
            searchHistory: [],
            keywords: [],
            Searchbooks: [],
        }
    },
    activated() {

        this.$store.commit('Eliminate');

        this.$store.commit('JudgeError');
        this.hotSearch = [];

        this.$store.commit('AcquireJudgeSearch', {
            Judge: true
        });

        this.SearchValue();

        this.axios.get(`hot-books`).then((respaonse)=>{
            for(let i=0;i<6;i++) {
                var obj = {
                    id: respaonse.data.newHotWords[i].book,
                    name: respaonse.data.newHotWords[i].word,
                }
                this.hotSearch.push(obj);
            }
        })

        this.GetSearchValue();

    },
    methods: {
        SearchValue() {
            var name = this.$store.state.SearchValue;
            if(name!="") {
                this.axios.get(`search?keyword=${name}`).then((respaonse)=>{
                    this.Searchbooks = []
                    var num = 0;
                    if(respaonse.data.books.length>20) {
                        num = 20
                    }else {
                        num = respaonse.data.books.length
                    }
                    for(let i=0; i<num; i++) {
                        var split = respaonse.data.books[i].cover.split("/");
                        var obj = {
                            id: respaonse.data.books[i]._id,
                            img: unescape(split[2]),
                            title: respaonse.data.books[i].title,
                            shortIntro: respaonse.data.books[i].shortIntro,
                            author: respaonse.data.books[i].author,
                        }
                        this.Searchbooks.push(obj)
                    }
                })
            }
        },
        GetSearchValue() {
            var name = this.$store.state.GetSearchValue;
            this.axios.get(`auto-complete?query=${name}`).then((respaonse)=>{
                this.keywords = respaonse.data.keywords
            })
        }
    },
    watch: {
        "$store.state.SearchValue"() {
            this.SearchValue();
        },
        "$store.state.HistorySearch"(n) {
            this.searchHistory = n
        },
        "$store.state.JudgeSearch"(n) {
            this.condition = n
        },
        "$store.state.GetSearchValue"() {
            this.GetSearchValue();
        }
    }
}
</script>

<style lang="less">
    #SearchBooks {
        background-color: #eeeeee;
    }
</style>