<template>

    <!-- 头部组件ClassifyHeaded(头部导航栏的组件2) -->

    <div id="ClassifyHeaded">
        <div class="text">
            <van-tabs
                color="#fb9f00"
                background="transparent"
                line-width="20px"
                v-model="classify"
            >
                <van-tab
                    v-for="(item, index) in route"
                    :key="index"
                    :title="item.name"
                    :to="item.router"
                >
                </van-tab>
            </van-tabs>
        </div>
        <div class="search" @click="ClickSeek" >
            <svg t="1609210109478" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="3277" width="22" height="22"><path d="M677.8 215.9C550.6 88.7 344.3 88.7 217 215.9 89.8 343.1 89.8 549.4 217 676.7c116 116 297.6 126.2 425.2 30.8L831 896.2c7.7 7.7 20.2 7.7 27.9 0l38.5-38.5c7.7-7.7 7.7-20.2 0-27.9L708.6 641.1c95.4-127.6 85.2-309.2-30.8-425.2z m-46 414.7C530 732.4 365 732.4 263.2 630.6c-101.8-101.8-101.8-266.8 0-368.6s266.8-101.8 368.6 0 101.7 266.8 0 368.6z" p-id="3278" fill="#646566"></path></svg>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        route: {
            type:Array
        }
    },
    data() {
        return {
            classify: null,
        }
    },
    mounted() {
        this.classify = this.$store.state.classify
    },
    methods: {
        ClickSeek() {
            this.$store.commit('JudgeError')
            this.$router.push({
                path:"/searchbooks",
            })
        }
    },
    watch: {
        '$store.state.classify'(n) {
            this.classify = n
        },
    }
}
</script>

<style lang="less">
    #ClassifyHeaded {
        position: fixed;
        left: 0px;
        top: 0px;
        z-index: 10;
        width: 100%;
        padding: 5px 0;
        background-color: #ffda33;
        .text {
            width: 120px;
            margin: 0 auto;
            .van-tabs--line .van-tabs__wrap {
                .van-tabs__nav  {
                    .van-tab {
                        font-size: 16px;
                        font-family: "黑体";
                        font-weight: 500;
                    }
                    .van-tab--active {
                        color: #222222;
                        font-size: 20px;
                        font-weight: 600;
                    }
                }
            }
        }
        .search {
            position: absolute;
            right: 0;
            top: 16px;
            display: flex;
            justify-content: flex-end;
            align-items: center;
            .icon {
                margin-right: 10px;
            }
        }
    }
</style>