<template>

    <!-- 搜索头部组件 -->

    <div id="SearchBooksHead">
        <div class="SearchBooksHead">
            <van-icon
                name="arrow-left"
                size="24"
                @click="onClickLeft"
            />
            <form action="/searchbooks">
                <van-search
                    v-model="value"
                    show-action
                    placeholder="请输入书名、作者或主要人物"
                    background="#ffda33"
                    @input="onInput"
                    @clear="onClear"
                >
                    <template #action>
                        <div @click="onSearch">搜索</div>
                    </template>
                </van-search>
            </form>
        </div>

    </div>

</template>

<script>
export default {
    data() {
        return {
            value: "",
        }
    },
    methods: {
        onClickLeft() {
            this.$store.commit('JudgeRight');
            this.$router.go(-1);
            this.$store.commit('AcquireGetSearchValue',{
                value: ""
            });
            this.$store.commit('AcquireSearchValueClear');
            this.value = ""
        },
        onSearch() {
            // console.log('搜索');
            if(this.value!="") {
                this.$store.commit('AcquireSearchValue', {
                    value: this.value
                });
                this.$store.commit('AcquireHistorySearch');
                this.$store.commit('AcquireJudgeAcquire',{
                    judge: false
                });
            }
        },
        onInput() {
            // console.log("输入");
            // console.log(this.value);
            this.$store.commit('AcquireGetSearchValue', {
                value: this.value
            });
            this.$store.commit('AcquireJudgeAcquire',{
                judge: true
            });
        },
        onClear() {
            // console.log("清除");
            this.$store.commit('AcquireSearchValueClear');
            this.$store.commit('AcquireJudgeSearch', {
                Judge: true
            });
            this.$store.commit('AcquireJudgeAcquire',{
                judge: true
            });
        }
    },
    watch: {
        value(n) {
            
            if(n=="") {
                // console.log('没内容');
                this.$store.commit('AcquireSearchValueClear');
                this.$store.commit('AcquireJudgeSearch', {
                    Judge: true
                });
                this.$store.commit('AcquireJudgeAcquire',{
                    judge: true
                });
            }else {
                // console.log('有内容');
                this.$store.commit('AcquireJudgeSearch', {
                    Judge: false
                });
            }
        },
        "$store.state.SearchValue"(n) {
            this.value = n
        },
    }
}
</script>

<style lang="less">
    #SearchBooksHead {
        background-color: #ffda33;
        .SearchBooksHead {
            padding: 10px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            form {
                flex: 1 0 auto;
                padding-left: 10px;
            }
            .van-search__content {
                border-radius: 6px;
                overflow: auto;
            }
            .van-search__action:active {
                background-color: transparent;
            }
            .van-search--show-action {
                padding: 0;
            }
        }
    }
</style>