<template>

    <!-- 推荐独有的组件的子组件 -->

    <div id="ExclusiveRecommendSon" @click="AccessDetails(item.id)" >

        <div class="img">
            <img :src="item.img">
        </div>

        <div class="text">
            <div class="top">
                <span class="name">{{item.title}}</span>
                <span class="score">
                    {{item.rating}}<i>分</i>
                </span>
            </div>
            <div class="center">
                <p>
                    {{item.longIntro}}
                </p>
            </div>
            <div class="bottom">
                <span class="writer">{{item.author}}</span>
                <span class="popularity">{{item.ratio}}万人气</span>
            </div>
        </div>

    </div>
</template>

<script>
export default {
    props: {
        item:{
            type:Object
        }
    },
    methods: {
        AccessDetails(id) {
            this.$store.commit('JudgeError')
            this.$router.push({
                path:"/noveldetails",
                query: {
                    id
                },
            })
        }
    },
}
</script>

