    #ExclusiveRecommendSon {
        display: flex;
        margin-bottom: 20px;
        .img {
            width: 24%;
            box-shadow: 0 0 1px rgba(0, 0, 0, 0.2);
            display: flex;
            align-items: center;
            img {
                width: 100%;
                min-height: 100px;
                vertical-align: bottom;
            }
        }
        .text {
            width: 74%;
            margin-left: 10px;
            display: flex;
            flex-wrap: wrap;
            .top {
                flex: 1;
                display: flex;
                justify-content: space-between;
                align-items:center;
                .name {
                    width: 80%;
                    font-family: "黑体";
                    font-size: 18px;
                    color: #222222;
                    display: -webkit-box;
                    -webkit-box-orient: vertical;
                    -webkit-line-clamp: 1;
                    overflow: hidden;
                    text-overflow: ellipsis;
                }
                .score {
                    line-height: 20px;
                    font-size: 18px;
                    font-weight: 600;
                    color: #fca000;
