<template>

    <!-- 通用热门榜、排行榜等等的组件 -->

    <div id="RankingList">
        
        <TitleComponent>
            <slot></slot>
        </TitleComponent>

        <ul>
            <GeneralListOne
                v-for="(item,index) in items"
                :key="index"
                :item="item"
            ></GeneralListOne>
        </ul>

    </div>
</template>

<script>
import GeneralListOne from '@/components/GeneralListOne.vue';
import TitleComponent from '@/components/TitleComponent.vue';

export default {
    components: {
        GeneralListOne,
        TitleComponent,
    },
    props: {
        Hitems: {
            type: Array
        },
        Daily: {
            type: Array
        },
        Ranking: {
            type: Array
        }
    },
    data() {
        return {
           items:[]
        }
    },
    mounted() {
        if(this.Hitems) {
            this.items = this.Hitems
        }
        if(this.Daily) {
            this.items = this.Daily
        }
        if(this.Ranking) {
            this.items = this.Ranking
        }
    },
    watch: {
        Hitems(n) {
            this.items = n
        },
        Daily(n) {
            this.items = n
        },
        Ranking(n) {
            this.items = n
        }
    }
}
</script>

<style lang="less">
    #RankingList {
        padding: 10px;
        ul {
            display: flex;
            flex-wrap: wrap;
        }
    }
</style>