<template>

    <!-- 书架 -->

    <div id="Bookrack">

        <BookrackRoof></BookrackRoof>

        <BookrackContent
            :recommend="recommend"
            :addbookcontent="addbookcontent"
        ></BookrackContent>

    </div>

</template>

<script>
import BookrackRoof from '@/components/BookrackRoof.vue';
import BookrackContent from '@/components/BookrackContent.vue';

export default {
    components: {
        BookrackRoof,
        BookrackContent,
    },
    data() {
        return {
            recommend: {},
            addbookcontent: [],
        }
    },
    created() {
        this.axios.get(`rank/550b8397de12381038ad8c0b`).then((respaonse)=>{
            this.recommend = {};
            var split = respaonse.data.ranking.books[0].cover.split("/");
            var obj = {
                img: unescape(split[2]),
                title: respaonse.data.ranking.books[0].title,
                shortIntro: respaonse.data.ranking.books[0].shortIntro,
                id: respaonse.data.ranking.books[0]._id,
            }
            this.recommend = obj
        })
    },
    activated() {

        this.$store.commit('Eliminate');

        var str = localStorage.getItem('key');
        this.addbookcontent = [];
        if(str) {
            var arr = str.split(',');
            for(let i=0;i<arr.length;i++) {
                this.axios.get(`book-info/${arr[i]}`).then((respaonse)=>{
                    var split = respaonse.data.cover.split("/");
                    var obj = {
                        img: unescape(split[2]),
                        title: respaonse.data.title,
                        author: respaonse.data.author,
                        id: respaonse.data._id,
                    }
                    this.addbookcontent.push(obj)
                })
            }
        }
    },
}
</script>

<style lang="less">
    #Bookrack {
        padding-top: 54px;
        padding-bottom: 54px;
    }
</style>