<template>

    <!-- 小说详情详情,查看目录 -->

    <div id="NovelDetailsParticulars">

        <div class="text">
            <h3>详情</h3>
            <p>
                {{DetailedMessage.longIntro}}
            </p>
        </div>

        <van-cell is-link @click="showPopup">
            <div class="text-cell">
                <h3>查看目录</h3>
                <span>{{DetailedMessage.isSerial}}</span>
                <span>共{{DetailedMessage.chaptersCount}}章</span>
            </div>
        </van-cell>
        <van-popup v-model="show" position="right" :style="{ width: '80%',height: '100%' }" >
            <div class="examine">
                <p>
                    {{DetailedMessage.title}}
                </p>
                <ul>
                    <li
                        v-for="(item,index) in ChapterSorting"
                        :key="index"
                        @click="clickChapter(index)"
                        :class="arrange==index?sign:''"
                    >
                        {{item.title}}
                    </li>
                </ul>
            </div>
        </van-popup>

    </div>

</template>

<script>
export default {
    props: {
        DetailedMessage: {
            type: Object
        },
        ChapterSorting: {
            type: Array
        }
    },
    data() {
        return {
            show: false,
            arrange: null,
            sign: "sign"
        }
    },
    methods: {
        showPopup() {
            this.show = true
        },
        clickChapter(index) {
            this.$store.commit('AcquireUpDownChapter',{
                index
            });
            this.$store.commit('JudgeError')
            this.$router.push({
                path:"/chaptercontent",
                query: {
                    id:this.DetailedMessage.id
                }
            })
        }
    },
    watch: {
        "$store.state.UpDownChapter"(n) {
            this.arrange = n
        }
    }
}
</script>

<style lang="less">
    #NovelDetailsParticulars {
        width: 100%;
        margin-bottom: 10px;
        background-color: #ffffff;
        .text {
            padding: 10px 16px ;
            h3 {
                width: 100%;
                padding: 10px 0 ;
                font-size: 18px;
                color: #222222;
            }
            p {
                font-size: 14px;
                color: #666666;
                line-height: 24px;
            }
        }
        .text-cell {
            display: flex;
            h3 {
                font-size: 16px;
                color: #222222;
                padding-right: 10px;
            }
            span {
                font-size: 12px;
                color: #969696;
                margin-right: 10px;
            }
        }
        .examine {
            p {
                width: 100%;
                font-size: 20px;
                text-align: center;
                line-height: 20px;
                padding: 16px;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 1;
                overflow: hidden;
                text-overflow: ellipsis;
            }
            ul {
                li {
                    padding: 0px 10px ;
                    margin-bottom: 14px;
                    display: -webkit-box;
                    -webkit-box-orient: vertical;
                    -webkit-line-clamp: 2;
                    overflow: hidden;
                    text-overflow: ellipsis;
                }
                .sign {
                    color: #b04f08;
                }
            }
        }
    }
</style>