<template>

    <!-- 章节目录子组件 -->

    <div id="ContentSon">

        <div class="examine">
            <p>
                {{chapter[0]}}
            </p>
            <ul>
                <li
                    v-for="(item,index) in chapter[1]"
                    :key="index"
                    @click="clickChapter(index)"
                    :class="arrange===index?sign:''"
                >
                    {{item.title}}
                </li>
            </ul>
        </div>

    </div>
    
</template>

<script>
export default {
    props: {
        chapter: {
            type: Array
        }
    },
    data() {
        return {
            sign: "sign",
            arrange: 0,
        }
    },
    methods: {
        clickChapter(index) {
            this.$store.commit('AcquireUpDownChapter',{
                index
            });
        }
    },
    watch: {
        "$store.state.UpDownChapter"(n) {
            this.arrange = n
        }
    }
}
</script>

<style lang="less">
    #ContentSon {
        .examine {
            p {
                width: 100%;
                font-size: 20px;
                line-height: 20px;
                padding: 0 16px;
                margin: 16px 0;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 1;
                overflow: hidden;
                text-overflow: ellipsis;
            }
            ul {
                li {
                    padding: 0px 10px ;
                    margin-bottom: 14px;
                    display: -webkit-box;
                    -webkit-box-orient: vertical;
                    -webkit-line-clamp: 2;
                    overflow: hidden;
                    text-overflow: ellipsis;
                }
                .sign {
                    color: #b04f08;
                }
            }
        }
    }
</style>