import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    active: 0 ,//首页切换功能的数字记录
    place: "/",//首页切换功能的路由地址记录
    classify: 0,//分类切换功能的数字记录
    classifyPlace: "/classify",//分类切换功能的路由地址记录
    judge: true,//判断底部的标签栏的显示与隐藏
    ShowUpDown: true,//显示隐藏的返回框和上下一章的框
    nightJudge: false,//切换白天黑夜模式的开关记录
    UpDownChapter: 0,//上下章节的加减
    SearchValue: "",//搜索的值（要按下搜索才能得到）
    HistorySearch: [],//历史搜索（点击删除清空）
    JudgeSearch: true,//判断数据是否出现覆盖内容
    GetSearchValue: "",//预搜索值(只要遍历上去就能用)
    JudgeAcquire: true,//判断是否点击了搜索按钮
    ContentBackgroundColor: "#e3d9bc",//章节内容背景颜色
    ContentFontSize: "20px",//章节字体大小
    CoatingJudge: false,//判断是否覆盖小说详情
    JudgeCover: false,//判断是否覆盖开始显示
  },
  mutations: {
    one (state) {
      state.active = 0
      state.place = "/"
    },
    two (state) {
      state.active = 1
      state.place = "/booktown/girl"
    },
    three (state) {
      state.active = 2
      state.place = "/booktown/boy"
    },
    classifyOne (state) {
      state.classify = 0
      state.classifyPlace = "/classify"
    },
    classifyTwo (state) {
      state.classify = 1
      state.classifyPlace = "/classify/classifyboy"
    },
    JudgeError (state) {
      state.judge = false
    },
    JudgeRight (state) {
      state.judge = true
    },
    ClickShowUpDown (state) {
      state.ShowUpDown = !state.ShowUpDown
    },
    ClicknightJudge (state) {
      state.nightJudge = !state.nightJudge
    },
    UpDownChapterAdd (state) {
      state.UpDownChapter++
    },
    UpDownChapterMinus (state) {
      state.UpDownChapter--
    },
    Eliminate (state) {
      state.UpDownChapter = 0
    },
    AcquireUpDownChapter (state,payload) {
      state.UpDownChapter = payload.index
    },
    AcquireSearchValue (state,payload) {
      state.SearchValue = payload.value
    },
    AcquireSearchValueClear (state) {
      state.SearchValue = ""
    },
    AcquireHistorySearch (state) {
      if(state.HistorySearch.indexOf(state.SearchValue)===-1) {
        state.HistorySearch.push(state.SearchValue)
      }
    },
    AcquireHistorySearchClear (state) {
      state.HistorySearch = []
    },
    AcquireJudgeSearch (state,payload) {
      state.JudgeSearch = payload.Judge
    },
    AcquireGetSearchValue (state,payload) {
      state.GetSearchValue = payload.value
    },
    AcquireJudgeAcquire (state,payload) {
      state.JudgeAcquire = payload.judge
    },
    AcquireContentBackgroundColor (state,payload) {
      state.ContentBackgroundColor = payload.color
    },
    AcquireContentFontSize (state,payload) {
      state.ContentFontSize = payload.size
    },
    AcquireCoatingJudge (state,payload) {
      state.CoatingJudge = payload.judge
    },
    AcquireJudgeCover (state,payload) {
      state.JudgeCover = payload.judge
    }
  },
  actions: {
  },
  modules: {
  }
})
