<template>

    <!-- 预搜索 -->

    <div id="PreliminarySearch">

        <ul>
            <li
                v-for="(item, index) in keywords"
                :key="index"
                @click="ClickSearch(item)"
            >
                <van-icon name="label-o" />
                <p>
                    {{item}}
                </p>
            </li>
        </ul>

    </div>

</template>

<script>
export default {
    props: {
        keywords: {
            type: Array
        }
    },
    data() {
        return {
        }
    },
    methods: {
        ClickSearch(item) {
            this.$store.commit('AcquireSearchValue',{
                value: item
            });
            this.$store.commit('AcquireJudgeAcquire',{
                judge: false
            });
            this.$store.commit('AcquireHistorySearch');
        }
    },
}
</script>

<style lang="less">
    #PreliminarySearch {
        background-color: #ffffff;
        ul {
            li {
                padding: 10px 20px;
                display: flex;
                align-items: center;
                p {
                    line-height: 24px;
                    padding-left: 10px;
                }
                border-bottom: 1px solid #dedede;
            }
        }
    }
</style>