<template>

    <!-- 小说详情 -->

    <div id="NovelDetails">

        <div class="NovelDetails">

            <van-nav-bar
                :title="title"
                style="background-color: rgba(255,255,255);"
                left-arrow
                fixed
                @click-left="onClickLeft"
            >
                <template #right>
                    <van-icon name="ellipsis" size="18" style="color:#fff;" />
                </template>
            </van-nav-bar>

            <NovelDetailsName
                :DetailedMessage="DetailedMessage"
            ></NovelDetailsName>

            <NovelDetailsScore
                :DetailedMessage="DetailedMessage"
            ></NovelDetailsScore>

            <NovelDetailsParticulars
                :DetailedMessage="DetailedMessage"
                :ChapterSorting="ChapterSorting"
            ></NovelDetailsParticulars>

            <NovelDetailsComment
                :DetailedComment="DetailedComment"
            ></NovelDetailsComment>

        </div>

        <ReadNovElentrance
            :id="id"
        ></ReadNovElentrance>

    </div>

</template>

<script>
import NovelDetailsName from '@/components/NovelDetailsName.vue';
import NovelDetailsScore from '@/components/NovelDetailsScore.vue';
import NovelDetailsParticulars from '@/components/NovelDetailsParticulars.vue';
import NovelDetailsComment from '@/components/NovelDetailsComment.vue';
import ReadNovElentrance from '@/components/ReadNovElentrance.vue';

export default {
    components: {
        NovelDetailsName,
        NovelDetailsScore,
        NovelDetailsParticulars,
        NovelDetailsComment,
        ReadNovElentrance
    },
    data() {
        return {
            title:"",
            DetailedMessage: {},
            DetailedComment: [],
            ChapterSorting: [],
            id: null,
        }
    },
    methods: {
        onClickLeft() {
            this.$store.commit('JudgeRight');
            this.$router.go(-1);
        }
    },
    activated() {

        this.$store.commit('AcquireCoatingJudge',{
            judge: true
        });

        this.$store.commit('JudgeError');
        var id = this.$router.history.current.query.id;
        this.id = id;

        this.axios.get(`book-info/${id}`).then((respaonse)=>{
            this.DetailedMessag = {}
            this.title = respaonse.data.title;
            var split = respaonse.data.cover.split("/");
            this.DetailedMessage = {
                id: respaonse.data._id,//id编号
                img: unescape(split[2]),//图片
                title: respaonse.data.title,//书名
                author: respaonse.data.author,//作者
                minorCate: respaonse.data.minorCate,//书籍种类
                isSerial: respaonse.data.isSerial?"连载":"完结",//是否完结
                wordCount: Math.floor(respaonse.data.wordCount/10000),//字数
                score: (respaonse.data.rating.score/1).toFixed(1),//评分
                scoreH: Math.ceil((+(respaonse.data.rating.score/1).toFixed(1)+0.2)*10)+"%",//评分百分百
                retentionRatio: (respaonse.data.retentionRatio/1).toFixed(1),//人气
                totalFollower: (respaonse.data.totalFollower/10000).toFixed(1),//在线看书人数
                longIntro: respaonse.data.longIntro,//简介内容
                chaptersCount: respaonse.data.chaptersCount,//章节数
            }
        })

        this.axios.get(`book/discussions?book=${id}`).then((respaonse)=>{
            this.DetailedComment = [];
            for(let i=10; i<respaonse.data.posts.length; i++) {
                var obj = {
                    title: respaonse.data.posts[i].title,//评论内容
                    likeCount: respaonse.data.posts[i].likeCount,//评论点赞
                    updatedDays: respaonse.data.posts[i].updated.split(/[A-Z]/g)[0],//评论日期
                    updatedTime: respaonse.data.posts[i].updated.split(/[A-Z]/g)[1].split(":")[0]+":"+respaonse.data.posts[i].updated.split(/[A-Z]/g)[1].split(":")[1],//评论时间
                    nickname: respaonse.data.posts[i].author.nickname,//评论者名称
                }
                this.DetailedComment.push(obj);
            }
        })

        this.axios.get(`book-sources?view=summary&book=${id}`).then((respaonse)=>{
            this.axios.get(`book-chapters/${respaonse.data[0]._id}`).then((respaonse)=>{
                this.ChapterSorting = [];
                for(let i=0; i<respaonse.data.chapters.length; i++) {
                    var obj = {
                        id: respaonse.data.chapters[i]._id,//id序号
                        title: respaonse.data.chapters[i].title,//章节信息
                        link: respaonse.data.chapters[i].link,//章节获取
                        order: respaonse.data.chapters[i].order,//章节排序
                    }
                    this.ChapterSorting.push(obj);
                }
            })
        }).finally(()=>{
            // console.log('完成');
            this.$store.commit('AcquireCoatingJudge',{
                judge: false
            });
        })
        
        
    }
}
</script>

<style lang="less">
    #NovelDetails {
        padding-bottom: 64px;
        background-color: #eeeeee;
        .NovelDetails {
            position: relative;
            width: 100%;
            padding-top: 46px;
            .van-nav-bar .van-icon {
                color: #222222;
            }
        }
    }
</style>