<template>

    <!-- 小说章节内容上部分 -->

    <div id="ChapterContentTop">

        <div class="ChapterContentTop">
            <van-nav-bar
                :title="title"
                left-text="返回"
                left-arrow
                @click-left="onClickLeft"
                @click-right="onClickRight"
            >
                <template #right>
                    <van-icon name="ellipsis" size="18" />
                </template>
            </van-nav-bar>
        </div>

    </div>
</template>

<script>
export default {
    props: {
        ContentTop: {
            type: Array
        }
    },
    data() {
        return {
            title: null,
        }
    },
    methods: {
        onClickLeft() {
            this.$store.commit('JudgeRight');
            this.$router.go(-1);
            var strId = localStorage.getItem('historyId');
            if(strId) {
                var arrId = strId.split(',');
                if(arrId.indexOf(this.ContentTop[0])===-1) {
                    arrId.push(this.ContentTop[0]);
                    localStorage.setItem('historyId',arrId.toString(','));
                }
            } else {
                localStorage.setItem('historyId',this.ContentTop[0]);
            }

            //第二

            var chapter = this.$store.state.UpDownChapter;

            var strChapter = localStorage.getItem('historychapter');
            if(strChapter) {
                var arrChapter = strChapter.split(',');
                for(var i=0;i<arrId.length;i++) {
                    if(arrId[i]===this.ContentTop[0]) {
                        arrChapter[i] = chapter
                        localStorage.setItem('historychapter',arrChapter.toString(','));
                    }
                }
            } else {
                localStorage.setItem('historychapter',chapter);
            }
            
        },
        onClickRight() {
            this.$store.commit('JudgeError')
            this.$router.push({
                path:"/noveldetails",
                query: {
                    id: this.ContentTop[0]
                },
            })
        }
    },
    watch: {
        ContentTop(n) {
            this.title = n[1]
        }
    }
}
</script>

<style lang="less">
    #ChapterContentTop {
        position: fixed;
        left: 0;
        top: 0;
        width: 100%;
        .van-nav-bar {
            background: none;
        }
        .ChapterContentTop {
            .van-nav-bar__content {
                background-color: #323232;
            }
            .van-nav-bar__text {
                color: #cbcbcb;
            }
            .van-nav-bar .van-icon {
                color: #cbcbcb
            }
            .van-nav-bar__title {
                color: #cbcbcb
            }
        }
    }
</style>