<template>

    <!-- 小说阅读入口 -->

    <div id="ReadNovElentrance">

        <div class="base" >
            <span @click="AddBookcase(id)" >
                <svg v-if="condition" t="1610019849943" class="icon" viewBox="0 0 1028 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="12971" width="28" height="28"><path d="M825.82 199.025C741.931 115.544 630.434 69.566 511.806 69.566S281.64 115.544 197.744 199.024 67.6 393.614 67.6 511.766s46.225 229.24 130.143 312.75 195.394 129.458 314.033 129.458 230.155-45.928 314.013-129.457 130.143-194.63 130.143-312.781-46.156-229.201-130.114-312.711z m-91.916 344.472H543.537v190.377c0 17.524-14.206 31.731-31.73 31.731s-31.731-14.207-31.731-31.73V543.496H289.698c-17.524 0-31.73-14.206-31.73-31.73s14.206-31.731 31.73-31.731h190.377V289.667c0-17.524 14.206-31.73 31.73-31.73 17.526 0 31.731 14.206 31.731 31.73v190.377h190.367c17.524 0 31.73 14.207 31.73 31.73s-14.206 31.732-31.73 31.732v-0.009z" p-id="12972" fill="#222222"></path></svg>
                <svg v-else t="1610019797296" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="10570" width="28" height="28"><path d="M512 0a512 512 0 0 1 512 512 512 512 0 0 1-512 512 512 512 0 0 1-512-512 512 512 0 0 1 512-512z m186.496 320.256L443.136 568.96 325.568 454.4 256 521.92l147.008 143.296 0.128-0.128 40.32 39.104L768 387.904l-69.504-67.648z" p-id="10571" fill="#999999"></path></svg>
                <i v-if="condition" >加书架</i>
                <i v-else >在书架</i>
            </span>

            <span>
                <svg t="1610020078390" class="icon" viewBox="0 0 1025 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="13854" width="28" height="28"><path d="M256.186967 384.002507l255.999284 256.000716 256.000716-256.000716L576.193594 384.002507 576.187863 0l-127.997493 0 0 384.002507L256.186967 384.002507zM0 603.167052l0.186251 420.832948 1024 0L1024.186251 603.167052l-41.83773 0-32.473597 0-53.488453 0-0.197713 292.836888L128.189475 896.00394 129.415867 603.167052M1024.187684 603.167052" p-id="13855" fill="#222222"></path></svg>
                <i>下载</i>
            </span>
        </div>

        <van-button
            type="primary"
            color="#fcc800"
            round
            @click="NovelChapterContent"
        >
            <i style="color:#222222;font-style:normal;font-size:16px;font-weight:600;" >立即免费阅读</i>
        </van-button>

    </div>
</template>

<script>
export default {
    props: {
        id: {
            type: String
        }
    },
    data() {
        return {
            condition: true,
        }
    },
    methods: {
        AddBookcase(id) {
            var str = localStorage.getItem('key');
            if(str) {
                var arr = str.split(',');
                if(arr.indexOf(id)===-1) {
                    this.condition = false
                    arr.push(id);
                    localStorage.setItem('key',arr.toString(','));
                }else {
                    this.condition = true
                    arr.splice(arr.indexOf(id),1)
                    localStorage.setItem('key',arr.toString(','));
                }
            } else {
                localStorage.setItem('key',id);
                this.condition = false
            }
            
        },
        NovelChapterContent() {
            this.$store.commit('JudgeError');
            this.$router.push({
                path: "/chaptercontent",
                query: {
                    id: this.id
                }
            })
        }
    },
    watch: {
        id(n) {
            this.condition=true
            var str = localStorage.getItem('key');
            if(str) {
                var arr = str.split(',')
                if(arr.indexOf(n)!==-1) {
                    this.condition=false
                }else {
                    this.condition=true
                }
            }
        }
    }
}
</script>

<style lang="less">
    #ReadNovElentrance {
        position: fixed;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 64px;
        background-color: #eee;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 16px;
        .base {
            display: flex;
            justify-content: space-around;
            width: 120px;
            span {
                display: flex;
                flex-wrap: wrap;
                justify-content: center;
                width: 50px;
                i {
                    font-size: 14px;
                    font-style: normal;
                }
            }
        }
    }
</style>