<template>

    <!-- 底部标签栏 -->

    <div id="Tabbar">

        <van-tabbar
            route
            active-color="#fcc50b"
            inactive-color="#bbbbbb"
        >
            <van-tabbar-item
                v-for="(item, index) in base"
                :key="index"
                :to="item.to"
                :icon="item.icon"
            >
                {{item.name}}
            </van-tabbar-item>
        </van-tabbar>

    </div>
</template>

<script>
export default {
    props: {
        base: {
            type:Array
        }
    }
}
</script>

