<template>

    <!-- 小说章节内容中部分 -->

    <div id="ChapterContentCenter"
        @click="ShowUpDown"
        :style="'backgroundColor:'+(nightJudge?bgcolorB:bgcolorA)"
    >
            <div
                class="ChapterContentCenter"
                :style="'color:'+(nightJudge?colorB:colorA)"
            >
                <h1>
                    {{titles}}
                </h1>
                <div class="article">
                    <p
                        v-for="(item, index) in items"
                        :key="index"
                        :style="{fontSize:fontSize}"
                    >
                        {{item}}
                    </p>
                </div>
            </div>

    </div>

</template>

<script>
export default {
    props: {
        cpContent: {
            type: Array
        },
        title: {
            type: String
        }
    },
    data() {
        return {
            nightJudge: false,
            bgcolorA: "#e3d9bc;",
            bgcolorB: "#181818;",
            colorA: "#383838;",
            colorB: "#999999;",
            fontSize: "20px",
            loading: true,
            items: [],
            titles: null,
        }
    },
    mounted() {
        this.nightJudge = this.$store.state.nightJudge;
        this.bgcolorA = this.$store.state.ContentBackgroundColor;
        this.fontSize = this.$store.state.ContentFontSize;
    },
    methods: {
        ShowUpDown() {
            this.$store.commit('ClickShowUpDown')
        },
    },
    watch: {
        title(n) {
            this.titles = n;
        },
        cpContent(n) {
            this.items = n
        },
        '$store.state.nightJudge'(n) {
            this.nightJudge = n
        },
        '$store.state.ContentBackgroundColor'(n) {
            this.bgcolorA = n
        },
        '$store.state.ContentFontSize'(n) {
            this.fontSize = n
        },
    }
}
</script>

<style lang="less">
    #ChapterContentCenter {
        width: 100%;
        height: 100%;
        min-height: 100vh;
        padding: 25px 0;
        .ChapterContentCenter {
            h1 {
                padding: 0 20px;
                line-height: 1.5;
                font-size: 26px;
                font-weight: 400;
            }
            .article {
                padding: 10px 20px 0;
                p {
                    margin: 10px 0;
                    line-height: 1.5;
                    text-indent: 2rem;
                    text-align: justify;
                    word-wrap: break-word;
                    word-break: normal;
                }
            }
        }
    }
</style>