<template>

    <!-- 书城推荐 -->

    <div id="Recommend">

        <ClassicNovel
            :Citems="Citems"
        ></ClassicNovel>

        <RankingList
            :Hitems="Hitems"
        >
            全网大热榜
        </RankingList>

        <ExclusiveRecommend
            :Romance="Romance"
        >
            必看言情
        </ExclusiveRecommend>

        <ExclusiveRecommend
            :ShuangWen="ShuangWen"
        >
            必看爽文
        </ExclusiveRecommend>

        <ExclusiveRecommend
            :HighMark="HighMark"
        >
            高分好书
        </ExclusiveRecommend>

    </div>
</template>

<script>
import RankingList from '@/components/RankingList.vue';
import ClassicNovel from '@/components/ClassicNovel.vue';
import ExclusiveRecommend from '@/components/ExclusiveRecommend.vue';

export default {
    components: {
        RankingList,
        ClassicNovel,
        ExclusiveRecommend,
    },
    data() {
        return {
            ClassicList: [
                "飞行家",
                "倚刀云燕",
                "彼时花胜雪"
            ],
            Citems: [],
            HotList: [
                "婚后危机",
                "腹黑邪王赖上门",
                "农门悍妻",
                "星海猎人",
                "鉴宝神瞳",
                "在林正英世界除妖秒速",
            ],
            Hitems: [],
            RomanceList: [
                "农女逆袭种田忙",
                "锦堂归燕",
                "农家宝妻"
            ],
            Romance: [],
            ShuangWenList: [
                "玄幻帝皇召唤系统",
                "奇门弄宝",
                "神级系统末世最强卡牌供应商"
            ],
            ShuangWen: [],
            HighMark: [],
            jused: false ,
        }
    },
    activated() {
        this.$store.commit('one');
        this.$store.commit('Eliminate');
    },
    created() {

        this.$store.commit('AcquireJudgeCover',{
            judge: true
        });

        // this.axios.get(`http://localhost:8080/json/recommend.json`).then((respaonse)=>{
        //     console.log(respaonse.data);
        //     for(let i=0; i<respaonse.data.citems.length ; i++) {
        //         var split1 = respaonse.data.citems[i].cover.split("/");
        //         var obj1 = {
        //             img: unescape(split1[2]),
        //             title: respaonse.data.citems[i].title,
        //             id: respaonse.data.citems[i]._id,
        //         }
        //         this.Citems.push(obj1);
        //     }
        //     for(let i=0; i<respaonse.data.hotList.length ; i++) {
        //         var split2 = respaonse.data.hotList[i].cover.split("/");
        //         var obj2 = {
        //             img: unescape(split2[2]),
        //             ratio: respaonse.data.hotList[i].retentionRatio+"万热度",
        //             title: respaonse.data.hotList[i].title,
        //             id: respaonse.data.hotList[i]._id,
        //         }
        //         this.Hitems.push(obj2);
        //     }
        // })

        for(let j=0; j<this.ClassicList.length ; j++){
            this.axios.get(`search?keyword=${this.ClassicList[j]}`).then((respaonse)=>{
                var split = respaonse.data.books[0].cover.split("/");
                var obj = {
                    img: unescape(split[2]),
                    title: respaonse.data.books[0].title,
                    id: respaonse.data.books[0]._id,
                }
                this.Citems.push(obj);
            }).finally(()=>{
                // console.log('完成');
                this.$store.commit('AcquireJudgeCover',{
                    judge: false
                });
            })
        }
        for(let i=0; i<this.HotList.length ; i++){
            this.axios.get(`search?keyword=${this.HotList[i]}`).then((respaonse)=>{
                var split = respaonse.data.books[0].cover.split("/");
                var obj = {
                    img: unescape(split[2]),
                    ratio: respaonse.data.books[0].retentionRatio+"万热度",
                    title: respaonse.data.books[0].title,
                    id: respaonse.data.books[0]._id,
                }
                this.Hitems.push(obj);
            })
        }
        for(let i=0; i<this.RomanceList.length ; i++){
            this.axios.get(`search?keyword=${this.RomanceList[i]}`).then((respaonse)=>{
                this.axios.get(`book-info/${respaonse.data.books[0]._id}`).then((respaonse)=>{
                    var split = respaonse.data.cover.split("/");
                    var obj = {
                        img: unescape(split[2]),
                        ratio: respaonse.data.retentionRatio,
                        title: respaonse.data.title,
                        id: respaonse.data._id,
                        author:respaonse.data.author,
                        longIntro:respaonse.data.longIntro,
                        rating:respaonse.data.rating.score
                    }
                    this.Romance.push(obj);
                })
            })
        }
        for(let i=0; i<this.ShuangWenList.length ; i++){
            this.axios.get(`search?keyword=${this.ShuangWenList[i]}`).then((respaonse)=>{
                this.axios.get(`book-info/${respaonse.data.books[0]._id}`).then((respaonse)=>{
                    var split = respaonse.data.cover.split("/");
                    var obj = {
                        img: unescape(split[2]),
                        ratio: respaonse.data.retentionRatio,
                        title: respaonse.data.title,
                        id: respaonse.data._id,
                        author:respaonse.data.author,
                        longIntro:respaonse.data.longIntro,
                        rating:respaonse.data.rating.score
                    }
                    this.ShuangWen.push(obj);
                })
            })
        }
        this.axios.get(`rank/5a68296bfc84c2b8ef9efdb0`).then((respaonse)=>{
            for(let i=0; i<10; i++){
                this.axios.get(`book-info/${respaonse.data.ranking.books[i]._id}`).then((respaonse)=>{
                    var split = respaonse.data.cover.split("/");
                    var obj = {
                        img: unescape(split[2]),
                        ratio: respaonse.data.retentionRatio,
                        title: respaonse.data.title,
                        id: respaonse.data._id,
                        author:respaonse.data.author,
                        longIntro:respaonse.data.longIntro,
                        rating:respaonse.data.rating.score
                    }
                    this.HighMark.push(obj);
                })
            }
        })

    },

}
</script>

