<template>

    <!-- 搜索书籍热门搜索 -->

    <div id="HotSearch">

        <div class="HotSearch">

            <h1>热门搜索</h1>

            <ul>
                <li
                    v-for="(item, index) in hotSearch"
                    :key="index"
                    @click="ClickHotSearch(item.id)"
                >
                    <span :style="'backgroundColor:'+ bgcolor[index]" >
                        {{index+1}}
                    </span>
                    <p>
                        {{item.name}}
                    </p>
                </li>
            </ul>

        </div>

    </div>

</template>

<script>
export default {
    props: {
        hotSearch: {
            type: Array
        }
    },
    data() {
        return {
            bgcolor: [
                "#fe4241",
                "#fe9643",
                "#fecc00",
                "#cbcbcb",
                "#cbcbcb",
                "#cbcbcb",
                "#cbcbcb",
                "#cbcbcb",
            ]
        }
    },
    methods: {
        ClickHotSearch(id) {
            // console.log(id);
            this.$store.commit('JudgeError');
            this.$router.push({
                path: "/chaptercontent",
                query: {
                    id
                }
            })
        }
    },
}
</script>

<style lang="less">
    #HotSearch {
        background-color: #ffffff;
        .HotSearch {
            padding: 20px;
            h1 {
                width: 100%;
                font-size: 14px;
                color: #999999;
                margin-bottom: 20px;
            }
            ul {
                display: flex;
                flex-wrap: wrap;
                li {
                    width: 50%;
                    padding: 10px 0;
                    display: flex;
                    align-items: center;
                    span {
                        padding: 2px 6px;
                        color: #ffffff;
                        border-radius: 3px;
                    }
                    p {
                        font-size: 15px;
                        padding-left: 8px;
                        color: #6b6b6b;
                        display: -webkit-box;
                        -webkit-box-orient: vertical;
                        -webkit-line-clamp: 1;
                        overflow: hidden;
                        text-overflow: ellipsis;
                    }
                }
            }
        }
    }
</style>