<template>

    <!-- 通用热门榜、排行榜等等的子组件 -->

    <li id="GeneralListOne" @click="AccessDetails(item.id)" >

        <div class="img">
            <van-image radius="2px" :src="item.img" lazy-load />
        </div>

        <div class="text">
            <p>{{item.title}}</p>
            <span>{{item.ratio}}</span>
        </div>

    </li>
</template>

<script>
export default {
    props: {
        item: {
            type: Object
        },
    },
    methods: {
        AccessDetails(id) {
            this.$store.commit('JudgeError')
            this.$router.push({
                path:"/noveldetails",
                query: {
                    id
                },
            })
        }
    },
}
</script>

<style lang="less">
    #GeneralListOne {
        width: 50%;
        display: flex;
        justify-content: center;
        margin-bottom: 10px;
        .img {
            width: 40%;
            display: flex;
            align-items: center;
            .van-image {
                width: 100%;
                min-height: 80px;
                vertical-align: bottom;
            }
        }
        .text {
            width: 60%;
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            align-items: center;
            p {
                font-size: 16px;
                padding: 0 8px;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 2;
                overflow: hidden;
                text-overflow: ellipsis;
            }
            span {
                font-size: 10px;
                color: #ff4a26;
            }
        }
    }
</style>