<template>
    <div id="app">
        
        <div
            v-if="nightJudge"
            class="night"
        ></div>

        <keep-alive>
            <router-view></router-view>
        </keep-alive>

        <RefreshUpload
            v-if="condition"
        ></RefreshUpload>

        <RefreshPage
            v-if="JudgeBegan"
        ></RefreshPage>

        <Tabbar
            :base="base"
            v-if="judge"
        ></Tabbar>

    </div>
</template>

<script>
import Tabbar from '@/components/Tabbar.vue';
import RefreshUpload from '@/components/RefreshUpload.vue';
import RefreshPage from '@/components/RefreshPage.vue';

export default {
    components: {
        Tabbar,
        RefreshUpload,
        RefreshPage,
    },
    data() {
        return {
            base: [
                {
                    name: "书城",
                    icon: "wap-home-o",
                    to: "/booktown"
                },
                {
                    name: "分类",
                    icon: "qr",
                    to: "/classify"
                },
                {
                    name: "书架",
                    icon: "bar-chart-o",
                    to: "/bookrack"
                },
                // {
                //     name: "我的",
                //     icon: "user-circle-o",
                //     to: "/my"
                // }
            ],
            judge: true,
            nightJudge: false,
            condition: false,
            JudgeBegan: false,
        }
    },
    mounted() {
        this.judge = this.$store.state.judge
        this.base[0].to = this.$store.state.place
        this.nightJudge = this.$store.state.nightJudge
        this.condition = this.$store.state.CoatingJudge
        this.JudgeBegan = this.$store.state.JudgeCover
    },
    watch: {
        "$store.state.place"(n) {
            this.base[0].to = n
        },
        "$store.state.classifyPlace"(n) {
            this.base[1].to = n
        },
        '$store.state.judge'(n) {
            this.judge = n
        },
        '$store.state.nightJudge'(n) {
            this.nightJudge = n
        },
        '$store.state.CoatingJudge'(n) {
            this.condition = n
        },
        '$store.state.JudgeCover'(n) {
            this.JudgeBegan = n
        }
    }
}
</script>

<style lang="less">
    #app {
        .night {
            position: fixed;
            left: 0;
            top: 0;
            z-index: 11;
            width: 100%;
            height: 100%;
            background-color: #333333;
            opacity: 0.4;
            pointer-events: none;
        }
    }
</style>


