import Vue from 'vue'
import VueRouter from 'vue-router'
import BookTown from '../views/BookTown.vue'
import Recommend from '../views/Recommend.vue'
import Classify from '../views/Classify.vue'
import NovelDetails from '../views/NovelDetails.vue'
import ChapterContent from '../views/ChapterContent.vue'
import Bookrack from '../views/Bookrack.vue'
import My from '../views/My.vue'
import SearchBooks from '../views/SearchBooks.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'BookTown',
    component: BookTown,
    children: [
      {
        path: '/',
        name: 'Recommend',
        component: Recommend
      },
      {
        path: '/booktown/girl',
        name: 'Girl',
        // route level code-splitting
        // this generates a separate chunk (girl.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "girl" */ '../views/Girl.vue')
      },
      {
        path: '/booktown/boy',
        name: 'Boy',
        // route level code-splitting
        // this generates a separate chunk (boy.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "boy" */ '../views/Boy.vue')
      },
    ],
  },
  {
    path: '/classify',
    name: 'Classify',
    component: Classify,
    children: [
      {
        path: '/classify',
        name: 'ClassifyGirl',
        // route level code-splitting
        // this generates a separate chunk (classifygirl.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "classifygirl" */ '../views/ClassifyGirl.vue')
      },
      {
        path: '/classify/classifyboy',
        name: 'ClassifyBoy',
        // route level code-splitting
        // this generates a separate chunk (classifyboy.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "classifyboy" */ '../views/ClassifyBoy.vue')
      },
    ]
  },
  {
    path: '/noveldetails',
    name: 'NovelDetails',
    component: NovelDetails,
  },
  {
    path: '/chaptercontent',
    name: 'ChapterContent',
    component: ChapterContent,
  },
  {
    path: '/bookrack',
    name: 'Bookrack',
    component: Bookrack,
  },
  {
    path: '/my',
    name: 'My',
    component: My,
  },
  {
    path: '/searchbooks',
    name: 'SearchBooks',
    component: SearchBooks,
  },
]

const router = new VueRouter({
  routes
})

export default router
