<template>

    <!-- 搜索覆盖层 -->

    <div id="SearchCoverage">

        <PreliminarySearch
            v-if="condition"
            :keywords="$attrs.keywords"
        ></PreliminarySearch>
        <TerminativeSearch
            v-else
            :Searchbooks="$attrs.Searchbooks"
        ></TerminativeSearch>

    </div>

</template>

<script>
import PreliminarySearch from '@/components/PreliminarySearch.vue';
import TerminativeSearch from '@/components/TerminativeSearch.vue';

export default {
    components: {
        PreliminarySearch,
        TerminativeSearch,
    },
    data() {
        return {
            condition: true
        }
    },
    watch: {
        "$store.state.JudgeAcquire"(n) {
            this.condition = n
        },
    }
}
</script>

